body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.article {
  margin: 1em 1em 1em 1em;
  width: 25em;
  background-color: rgba(255, 255, 255, .6);
}

img {
  border: solid thin black;
}

.container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin-top: 2.5em;
}

section {
  float: left;
  color: white;
  width: 16em;
  height: 100%;
  z-index: 1;
  top: 0;
  left: 0;
  overflow-x: hidden;
  position: fixed;
  padding: 1em 1em 1em 1em;
  border-right: solid black 4px;
  background-color: rgb(29, 47, 65);
}

img.profile {
  border: .25em solid lightseagreen;
  border-radius: 50%;
  width: 120px;
}
.App {
  text-align: center;
  margin-left: 18em;
  font-family: 'Lora', serif;
}

body {
  height: 100%;
  background-image: url(/projects-portfolio/static/media/white-waves.a06a3248.png)
}

p {
  font-size: small;
}

h1 {
  font-weight: bolder;
  font-size: 50px;
  margin-top: .5em;
}

a {
  font-size: small;
}
